.ProjectSelector {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    background-color: #fafafa;
    border-top: 1px solid #f0f0f0;
    &__wrapper {
        box-sizing: border-box;
        padding: 0 20px;
        display: flex;
        align-items: center;
        min-height: 35px;
        font-size: 14px;
        justify-content: flex-start;
        gap: 20px;
    }
    &__title {
        font-weight: bold;
        color: var(--text-color)
    }
    &__list {
        display: inline-flex;
        align-items: center;
        justify-content: flex-start;
        gap: 10px;
        &__item {
            text-decoration: none;
            color: var(--text-color);
            transition: all 150ms ease-in-out;
            &:hover {
                cursor: pointer;
                text-decoration: underline;
                color: var(--accent-color-hover-5);
            }
        }
    }
    &__back {
        border-left: 1px solid var(--border-color);
        padding-left: 20px;
        font-weight: bold;
        color: var(--accent-color);
        text-decoration: none;
        &:hover {
            cursor: pointer;
            text-decoration: underline;
        }
    }
}